
<template>
<div>
  <el-button @click="resetDateFilter">清除日期过滤器</el-button>
  <el-button @click="clearFilter">清除所有过滤器</el-button>
  <el-table
      row-key="date"
      ref="filterTable"
      :data="tableData"
      style="width: 100%">
    <el-table-column
        prop="date"
        label="日期"
        sortable
        width="180"
        column-key="date"
        :filters="[{text: '2016-05-01', value: '2016-05-01'}, {text: '2016-05-02', value: '2016-05-02'}, {text: '2016-05-03', value: '2016-05-03'}, {text: '2016-05-04', value: '2016-05-04'}]"
        :filter-method="filterHandler"
    >
    </el-table-column>
    <el-table-column
        prop="name"
        label="姓名"
        width="180">
    </el-table-column>
    <el-table-column
        prop="address"
        label="地址"
        :formatter="formatter">
    </el-table-column>
    <el-table-column
        prop="tag"
        label="标签"
        width="100"
        :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
      <template #default="scope">
        <el-tag
            :type="scope.row.tag === '家' ? 'primary' : 'success'"
            disable-transitions>{{scope.row.tag}}</el-tag>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
export default {
  name: "ScreeningTable",
  data() {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园',
        tag: '家'
      }, {
        date: '2016-05-04',
        name: '小虎',
        address: '湖北省武汉市 九峰森林 动物园',
        tag: '公司'
      }, {
        date: '2016-05-01',
        name: '小虎',
        address: '湖北省武汉市 东湖飞鸟世界 动物园',
        tag: '家'
      }, {
        date: '2016-05-03',
        name: '小虎',
        address: '湖北省武汉市 冰雪世界 动物园',
        tag: '公司'
      }]
    }
  },
  methods: {
    resetDateFilter() {
      this.$refs.filterTable.clearFilter('date');
    },
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    filterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    }
  }
}
</script>

